import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";

const dispatchedEvents = [];

/*
    Example: <srs-image src="/blah/blah.jpg" alt="Image" height="100px" width="100px"/>
*/
const SrsImage = function (props, ref) {
    return (
        <img src={props.src} alt={props.alt} width={props.width} height={props.height} />
    );
}

const styles = `
    
`
const props = [{
    name: "src",
    defaultValue: "#",
    type: "string"
}, {
    name: "alt",
    defaultValue: "",
    type: "string"
}, {
    name: "width",
    defaultValue: "",
    type: "string"
}, {
    name: "height",
    defaultValue: "",
    type: "string"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsImage), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };